* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#zoom {
  width: 100%;
  min-height: 100vh;
  color: #fff;
  background-color: #000;
  grid-template-rows: 30px 1fr 40px;
  gap: 5px;
  padding-bottom: 20px;
  display: grid;
  overflow: hidden;
}

#zoom header {
  text-align: center;
  color: #fff;
  background-color: #ffffff80;
  padding: 5px;
}

#zoom section {
  width: 90%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  margin: auto;
  display: flex;
}

#zoom section div {
  width: 300px;
  height: 18vh;
  background-color: #ffffff73;
  border-radius: 5px;
  align-items: flex-end;
  display: flex;
  position: relative;
}

#zoom section div h4 {
  background-color: #00000092;
  border-radius: 5px;
  margin: 5px;
  padding: 2px 5px;
  font-weight: 300;
}

