#zoom footer nav {
  height: 60px;
  background-color: #302f2f;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  font-size: 90%;
  display: flex;
  position: relative;
}

#zoom footer nav #check {
  visibility: hidden;
  z-index: -1;
  position: absolute;
  left: 0;
}

#zoom footer nav .first-icon, #zoom footer nav .second-icon {
  display: none;
  position: absolute;
