  left: 2%;
}

#zoom footer nav div {
  height: 100%;
}

#zoom footer nav .nav {
  width: 60%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#zoom footer nav .nav .first-nav {
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
}

#zoom footer nav .nav .first-nav div {
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 10px;
